.card {
  width: 41.48vh;
  height: 48.4vh;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 1.2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
}
.greenCheck {
  width: 3.5vh;
  height: 3.5vh;
}
.mainImage {
  width: 34.88vh;
  height: 22.5vh;
  background: #f6f8fb;
  box-sizing: border-box;
  border-radius: 1.2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.title {
  font-style: normal;
  font-weight: 500;
  font-size: 1.8vh;
  line-height: 2.7vh;
  text-align: center;
  letter-spacing: -0.035em;
  margin: 0;
  color: #4f4f4f;
}
.imageLink {
  position: relative;
  width: 34.88vh;
  height: 3.4vh;
  input {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 0.8vh;
    border: 0.1vh solid #e0e0e0;
    background: #f6f8fb;
    outline: none;
    font-style: normal;
    font-weight: 500;
    font-size: 1vh;
    letter-spacing: -0.035em;
    color: #4f4f4f;
  }
  button {
    right: -0.4vh;
    top: 0.27vh;
    position: absolute;
    width: 27%;
    height: 98%;
    background: #2f80ed;
    border-radius: 0.8vh;
  }
}
