.card {
  width: 41.48vh;
  height: 48.4vh;
  background: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 1.2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.title {
  h1 {
    font-size: 1.85vh;
    font-style: normal;
    font-weight: 500;
    line-height: 2.78vh;
    letter-spacing: -0.035em;
    color: #4f4f4f;
    margin: 0;
  }
}
.subtitle {
  h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 1vh;
    line-height: 1.5vh;
    text-align: center;
    letter-spacing: -0.035em;
    margin: 0;
    color: #828282;
  }
}
.imageDropper {
  width: 34.88vh;
  height: 22.5vh;
  background: #f6f8fb;
  border: 1px dashed #97bef4;
  box-sizing: border-box;
  border-radius: 1.2vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  img {
    width: 11.76vh;
    height: 9vh;
  }
  h4 {
    font-weight: 500;
    font-size: 1.2vh;
    line-height: 1.8vh;
    letter-spacing: -0.035em;
    color: #bdbdbd;
    margin: 0;
  }
}
.or {
  h3 {
    font-size: 1.2vh;
    line-height: 1.8vh;
    text-align: center;
    letter-spacing: -0.035em;
    color: #bdbdbd;
    margin: 0;
  }
}
button {
  width: 10.4vh;
  height: 3.3vh;
  background: #2f80ed;
  border-radius: 0.8vh;
  font-family: "Noto Sans", sans-serif;
  font-size: 1.2vh;
  line-height: 1.6vh;
  text-align: center;
  letter-spacing: -0.035em;
  border: none;
  outline: none;
  cursor: pointer;
  color: #ffffff;
}
