.loadingCard{position:absolute;width:41.27vh;height:14.75vh;top:calc(50% - (14.75vh / 2));left:calc(50% - (41.27vh / 2));display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;background:#fff;box-shadow:0vh .4vh 1.2vh rgba(0,0,0,.1);border-radius:1.2vh;-webkit-align-items:flex-start;align-items:flex-start;-webkit-justify-content:space-evenly;justify-content:space-evenly;padding-left:4vh}.loadingCard h1{font-family:Poppins;font-style:normal;font-weight:500;font-size:1.8vh;line-height:2.7vh;letter-spacing:-0.035em;color:#4f4f4f;margin:0}.loadingCard .uploadBar{width:35vh;height:.6vh;background:#f2f2f2;border-radius:.8vh;position:relative}.loadingCard .progressBar{width:10vh;height:.6vh;background:#2f80ed;border-radius:8px;position:relative;-webkit-animation:desplazamiento 2s infinite linear;animation:desplazamiento 2s infinite linear}@-webkit-keyframes desplazamiento{0%{left:0vh}50%{left:25vh}100%{left:0vh}}@keyframes desplazamiento{0%{left:0vh}50%{left:25vh}100%{left:0vh}}
.card{width:41.48vh;height:48.4vh;background:#fff;box-shadow:0px 4px 12px rgba(0,0,0,.1);border-radius:1.2vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly;position:relative}.greenCheck{width:3.5vh;height:3.5vh}.mainImage{width:34.88vh;height:22.5vh;background:#f6f8fb;box-sizing:border-box;border-radius:1.2vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly}.title{font-style:normal;font-weight:500;font-size:1.8vh;line-height:2.7vh;text-align:center;letter-spacing:-0.035em;margin:0;color:#4f4f4f}.imageLink{position:relative;width:34.88vh;height:3.4vh}.imageLink input{position:absolute;width:100%;height:100%;border-radius:.8vh;border:.1vh solid #e0e0e0;background:#f6f8fb;outline:none;font-style:normal;font-weight:500;font-size:1vh;letter-spacing:-0.035em;color:#4f4f4f}.imageLink button{right:-0.4vh;top:.27vh;position:absolute;width:27%;height:98%;background:#2f80ed;border-radius:.8vh}
