.loadingCard {
  position: absolute;
  width: 41.27vh;
  height: 14.75vh;
  top: calc(50% - (14.75vh / 2));
  left: calc(50% - (41.27vh / 2));
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0vh 0.4vh 1.2vh rgba(0, 0, 0, 0.1);
  border-radius: 1.2vh;
  align-items: flex-start;
  justify-content: space-evenly;
  padding-left: 4vh;
  h1 {
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 1.8vh;
    line-height: 2.7vh;
    letter-spacing: -0.035em;
    color: #4f4f4f;
    margin: 0;
  }
  .uploadBar {
    width: 35vh;
    height: 0.6vh;
    background: #f2f2f2;
    border-radius: 0.8vh;
    position: relative;
  }
  .progressBar {
    width: 10vh;
    height: 0.6vh;
    background: #2f80ed;
    border-radius: 8px;
    position: relative;
    animation: desplazamiento 2s infinite linear;
  }
  @keyframes desplazamiento {
    0% {
      left: 0vh;
    }
    50% {
      left: 25vh;
    }
    100% {
      left: 0vh;
    }
  }
}
